import React from 'react'

export default function WorkCard(props) {
    return (
        <div className="cont">
            <div className="left">
                <img src={props.data.image} />
            </div>
            <div className="right">
                <a class="title" href={props.data.link} target="_blank"> <h2>{props.data.title}</h2> </a>
                <p>{props.data.description}</p>
            </div>
            <style jsx>{`
                .cont{
                    width: 100%;
                    height: 400px;
                    display: grid;
                    grid-template-columns: 40% 40%;
                    grid-gap: 5%;
                    margin: 0;
                    padding: 0;
                }
                .left{
                    height: 100%;
                    overflow: hidden;
                }
                .left img {
                    width: 100%;
                    object-fit: cover;
                    object-position: top;
                    margin: 0;
                    padding: 0;
                }
                .title{
                    color: #fff;
                    text-decoration: none;
                }
            `}</style>
        </div>
    )
}
